import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);
import axios from "axios";

axios.defaults.headers = {
    'deviceid': 'device_for_web',
}
export const store = new Vuex.Store({

  state: {
    dataHome: [],
    dataHot: [],
    dataSpecial: [],
    dataSlide: [],
    dataPromotion: [],
    dataGo2joy: [],
    dataHotelDetail: [],
    dataHotelDetailSlide: [],
    dataHotelDetailflashSaleList: [],
    dataHotelDetailfacilityFormList: [],
    dataHotelDetailPolicy: [],
    dataHotelDetailComment: [],
    dataPromotionDetail: [],
    dataPromotionDetailSlide: [],
    dataListAddress: [],
    dataStartDetail: [],
    dataListHotelPromotionDetail: [],
  },
  mutations: {
    SET_DATA_LIST: (state, dataHome) => {
        state.dataHome = dataHome;
        // console.log('data1',state.dataHome)
    },
    SET_DATAHOT_LIST: (state, dataHot) => {
        state.dataHot = dataHot;
        // console.log('data2',dataHot)
    },
     SET_DATASPECIAL_LIST: (state, dataSpecial) => {
        state.dataSpecial = dataSpecial;
        // console.log('data3',state.dataSpecial)
    },
     SET_DATASLIDE_LIST: (state, dataSlide) => {
        state.dataSlide = dataSlide;
        // console.log('data4',state.dataSlide)
    },
     SET_DATAPROMOTION_LIST: (state, dataSlide) => {
        state.dataSlide = dataSlide;
        // console.log('data4',state.dataPromoiton)
    },
     SET_DATAGO2JOY_LIST: (state, dataGo2joy) => {
        state.dataGo2joy = dataGo2joy;
        // console.log('data4',state.dataPromoiton)
    },


    // Hotel detail
    SET_DATA_HOTELDETAIL: (state, dataHotelDetail) => {
        state.dataHotelDetail = dataHotelDetail;
        // console.log('data5 hoteldetail',state.dataHotelDetail)
    },
    SET_DATA_HOTELDETAIL_SLIDE: (state, dataHotelDetailSlide) => {
        state.dataHotelDetailSlide = dataHotelDetailSlide;
        // console.log('dataSlide hoteldetail',state.dataHotelDetailSlide)
    },
    SET_DATA_HOTELDETAIL_FLASHSALELIST: (state, dataHotelDetailflashSaleList) => {
        state.dataHotelDetailflashSaleList = dataHotelDetailflashSaleList;
        // console.log('dataflashSaleList hoteldetail',state.dataHotelDetailflashSaleList)
    },
     SET_DATA_HOTELDETAIL_FACILITY: (state, dataHotelDetailfacilityFormList) => {
        state.dataHotelDetailfacilityFormList = dataHotelDetailfacilityFormList;
        // console.log('datafacilityFormList hoteldetail',state.dataHotelDetailfacilityFormList)
    },
    SET_DATA_HOTELDETAIL_POLICY: (state, dataHotelDetailPolicy) => {
        state.dataHotelDetailPolicy = dataHotelDetailPolicy;
        // console.log('datafacilityFormList hoteldetail',state.dataHotelDetailPolicy)
    },
     SET_DATA_HOTELDETAIL_COMMENT: (state, dataHotelDetailComment) => {
        state.dataHotelDetailComment = dataHotelDetailComment;
        // console.log('datafacilityFormList hoteldetail',state.dataHotelDetailComment)
    },


    // Promotion detail
     SET_DATA_PROMOTION_DETAIL: (state, dataPromotionDetail) => {
        state.dataPromotionDetail = dataPromotionDetail;
        // console.log('datafacilityFormList hoteldetail',state.dataHotelDetailComment)
    },
     SET_DATA_PROMOTION_DETAIL_SLIDE: (state, dataPromotionDetailSlide) => {
        state.dataPromotionDetailSlide = dataPromotionDetailSlide;
        // console.log('datafacilityFormList hoteldetail',state.dataHotelDetailComment)
    },

    // Start hotel detail
    SET_DATA_START_DETAIL: (state, dataStartDetail) => {
        state.dataStartDetail = dataStartDetail;
    },

    SET_DATA_LIST_HOTEL_PROMOTIONDETAIL: (state, dataListHotelPromotionDetail) => {
        state.dataListHotelPromotionDetail = dataListHotelPromotionDetail;
    },


  
  },
  actions: {
    GET_DATA_LIST_HOTEL: async ({ commit }) => {
        // console.log(context, data)
        let {data} = await axios.get('https://api.go2joy.vn/hotelapi/home/view/findHomePageInfo')
        // console.log('data',data)
        commit("SET_DATA_LIST", data);
        commit('SET_DATAHOT_LIST',data.detailCollectionList[0].hotelFormList);
        commit('SET_DATASPECIAL_LIST',data.detailCollectionList[1].hotelFormList);
        commit('SET_DATAGO2JOY_LIST',data.detailCollectionList[2].hotelFormList);
        commit('SET_DATASLIDE_LIST',data.bannerFormList);
        commit('SET_DATAPROMOTION_LIST',data.bannerFormList);
        
    },
    GET_DATA_LIST_HOTELDETAIL: async ({ commit }, sn) => {
        // console.log(context, data)
        let datasn = sn
        let {data} = await axios.get(`https://api.go2joy.vn/hotelapi/hotel/view/viewHotelDetailNew?hotelSn=${datasn}`)
        // console.log('dataHotel detail ',data.data)
        commit("SET_DATA_HOTELDETAIL", data.data);
        commit("SET_DATA_HOTELDETAIL_SLIDE", data.data.justHotelImageList);
        commit("SET_DATA_HOTELDETAIL_FLASHSALELIST", data.data.flashSaleList);
        commit("SET_DATA_HOTELDETAIL_FACILITY", data.data.facilityFormList);
        commit("SET_DATA_HOTELDETAIL_POLICY", data.data.resSettingForm);
        commit("SET_DATA_HOTELDETAIL_COMMENT", data.data.userReviewFormList);
    },
    GET_DATA_PROMOTION_DETAIL: async ({ commit },{ sn ,action }) => {
        console.log(sn, action)
        let {data} = await axios.get(`https://api.go2joy.vn/hotelapi/promotion/view/findPromotionForAppNew?sn=${sn}&event=${action}`)
        // console.log('promotion detail ',data.data)
        commit("SET_DATA_PROMOTION_DETAIL", data.data);  
        //  console.log('data promotion detail', data.data)
        commit("SET_DATA_PROMOTION_DETAIL_SLIDE", data.data.promotionImageFormList);  
    },
    GET_DATA_START_DETAIL: async ({ commit }, sn) => {
        let {data} = await axios.get(`https://api.go2joy.vn/hotelapi/user/view/findUserReviewList2?hotelSn=${sn}&offset=0&limit=1`)
        // console.log('data store state ',data)
        commit("SET_DATA_START_DETAIL", data);  
        // commit("SET_DATA_PROMOTION_DETAIL_SLIDE", data.data.promotionImageFormList);  
    },



    GET_DATA_LIST_HOTEL_PROMOTIONDETAIL: async ({ commit }, sn) => {
        let {data} = await axios.get(`https://api.go2joy.vn/hotelapi/promotion/view/findLimitHotelAcceptProListForAppNew?promotionSn=${sn}&offset=0&longitude=106.6813272&latitude=10.7642163&sort=0&limit=10`)
        console.log('data store state ',data.data)
        commit("SET_DATA_LIST_HOTEL_PROMOTIONDETAIL", data.data);  
        // commit("SET_DATA_PROMOTION_DETAIL_SLIDE", data.data.promotionImageFormList);  
    },

    
  }
});



